'use client';

import { useEffect, useState } from 'react';
import { Button } from '@/components/ui/button';
import { useTheme } from 'next-themes';
import { MoonIcon, SunIcon } from 'lucide-react';

const ModeToggle = () => {
  const [mounted, setMounted] = useState(false);
  const { theme, resolvedTheme, setTheme } = useTheme();

  useEffect(() => {
    setMounted(true);

    // If first visit or previously set to "system", force to dark
    const stored = typeof window !== 'undefined' ? localStorage.getItem('theme') : null;
    if (!stored || stored === 'system') {
      setTheme('dark');
    }

    // If theme prop is "system" from earlier versions, coerce to a concrete theme
    if (theme === 'system') {
      setTheme(resolvedTheme ?? 'dark');
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  if (!mounted) return null;

  const current = theme === 'system' ? resolvedTheme ?? 'dark' : theme;
  const isDark = current === 'dark';

  const toggleTheme = () => setTheme(isDark ? 'light' : 'dark');

  return (
    <Button
      variant="ghost"
      aria-label={isDark ? 'Switch to light theme' : 'Switch to dark theme'}
      aria-pressed={isDark}
      onClick={toggleTheme}
      className="p-2 rounded-full transition hover:scale-105 hover:bg-accent focus-visible:ring-0"
    >
      {isDark ? (
        <MoonIcon className="w-5 h-5 text-foreground transition-transform duration-300" />
      ) : (
        <SunIcon className="w-5 h-5 text-foreground transition-transform duration-300" />
      )}
      <span className="sr-only">{isDark ? 'Dark mode' : 'Light mode'}</span>
    </Button>
  );
};

export default ModeToggle;
